import styled from "styled-components";
import BannerSlide from "../BannerSlide";

const Animalhospital = () => {
  return (
    <Container>
      <Ul>
        <Li>
                 
            ,  .
        </Li>
        <Li>  :    </Li>
        <Li>(  )</Li>
      </Ul>
      <BannerSlide />
    </Container>
  );
};

export default Animalhospital;

const Container = styled.div`
  height: 100vh;
`;

const Ul = styled.ul`
  padding-left: 20px;
`;

const Li = styled.li`
  list-style: circle;
  list-style-position: inside;
  font-size: 18px;
  line-height: 200%;
`;
